import {
	api
} from './api'
import {
	urlArr
} from './urlconfig'
import pair_input from './pair-input'
import {
	showMethod
} from './showmethod'
import {
	showLog
} from './showlog'
import * as mainHandle from './mainHandle'

let temp = `<form class="layui-form  getpair-tip tip" action="#">
	<div class="layui-form-item">
	  <label class="layui-form-label">getpair a deviceGET</label>
	</div>
	<fieldset class="layui-elem-field layui-field-title">
	  <legend  i18n='arguments'></legend>
	</fieldset>
	
	<div class="layui-form-item">
	  <label class="layui-form-label">pair code:</label>
	  <div class="layui-input-inline">
		<input type="text" name="pair code"  placeholder="CC:1B:E0:E0:10:C1" value="" lay-verify='deviceMac'  class="layui-input">
	  </div>
	  <div class="layui-form-mid layui-word-aux" i18n = 'required'>()</div>
	</div>
	
	
	<fieldset class="layui-elem-field layui-field-title">
	  <legend i18n = 'description'></legend>
	</fieldset>
	<div class="layui-form-item layui-form-text">
	  <div class="descriptors getpair-des">
		<p i18n="getpair-Tip-p1"><b>chip</b>01queryString(?chip=0?chip=1)11</p>
		<p i18n="getpair-Tip-p2"><b>deviceMac</b>MAC</p>
		<p i18n="getpair-Tip-p3"><b>type</b>bodyMACrandompublictypetype</p>
	  </div>
	</div>
	<div class="layui-form-item">
	  <div class="layui-input-block">
		<button class="layui-btn" lay-submit lay-filter="getpair">do</button>
	  </div>
	</div>
  </form>`

function gopair(deviceMac, etarget) {

	const url = urlArr.pair.replace("*deviceMac*", deviceMac),
		parent = $('#pairLog ul')
	let ajaxResult = api.pair(url)
	showMethod('pair')
	ajaxResult.done(function (e) {
		// debugger
		if (e.pairingStatus === "Passkey Input Expected") {
			$('.pairCode').show()
			$('.sub').unbind('click').bind('click',function () {
				var x = $('.Code').val()
				$('.pairCode').hide()
				mainHandle.linkage(10)
				pair_input(deviceMac, x)
			})
			$('.close').unbind('click').bind('click',function(){
				$('.pairCode').hide()

			})
			showLog(parent, {
				before: `mac:&nbsp;&nbsp;${deviceMac} pair`,
				message: JSON.stringify(e, null, 2),
				class: 'success'
			})
			$(`.l3 ul li[data-mac='${deviceMac}']`).slideUp('normal', function () {
				this.remove()
			});
		}else if (e.pairingStatus === 'Pairing Successful') {
			alert("")
			showLog(parent, {
				before: `mac:&nbsp;&nbsp;${deviceMac} pair`,
				message: JSON.stringify(e, null, 2),
				class: 'success'
			})
			$(`.l3 ul li[data-mac='${deviceMac}']`).slideUp('normal', function () {
				this.remove()
			});
		}else {
			alert('')
		}
	}).fail(function (e) {
		showLog(parent, {
			before: `mac:&nbsp;&nbsp;${deviceMac} pair`,
			message: JSON.stringify(e, null, 2),
			class: 'fail'
		})
		console.log(e)
	})

}



export default gopair


